.message {
  margin: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.imageAndText {
  display: flex;
  align-items: center;
  flex-direction: row-reverse;

  justify-content: flex-end;
}

.imageAndText img {
  margin-left: 20px;
  width: 30px;
  height: 30px;
}

.text {
  max-width: 600px;
  background-color: #06C;
  color: #000;
  padding: 10px;
  border-radius: 10px;
}

.text:after {
}

.name {
  color: #ddeeff;
  font-size: 16px;
}

.messageText {
  color: #ddeeff;
  font-size: 10px;
}

.time {
  display: flex;
  justify-content: flex-end;
}
